.loadding {
  display: none;
}
.pyg_header {
  padding: 5px;
  width: 100%;
  height: 45px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
}
.pyg_footer {
  width: 100%;
  height: 45px;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
}
.pyg_footer a {
  padding: 5px;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #000;
}
.pyg_footer a i {
  font-size: 20px;
}
.pyg_footer a div {
  font-size: 14px;
}
.pyg_view {
  overflow: scroll;
  background-color: #eee;
}
html,
body,
.pyg_view {
  height: 100%;
}
body {
  padding-top: 45px;
  padding-bottom: 45px;
}
* {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
.loadding::before {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0.5;
  content: "";
  z-index: 100;
}
.loadding::after {
  font: normal normal normal 14px/1 FontAwesome;
  position: fixed;
  top: 40%;
  left: 50%;
  margin-left: -50px;
  font-size: 100px;
  color: #0094ff;
  content: "\f013";
  z-index: 101;
  animation: fa-spin 1s infinite linear;
}
@media screen and (min-width: 375px) {
  html {
    font-size: 18.75px;
  }
}
@media screen and (min-width: 750px) {
  html {
    font-size: 37.5px;
  }
}
.pyg_view {
  height: 100%;
  overflow: hidden;
  display: flex;
}
.pyg_view .left_box {
  width: 80px;
}
.pyg_view .left_box .left_memu {
  list-style: none;
  padding: 0;
  margin: 0;
}
.pyg_view .left_box .left_memu li {
  height: 40px;
  line-height: 40px;
  background-color: #eee;
  text-align: center;
}
.pyg_view .left_box .left_memu li.active {
  background-color: #fff;
  border-left: 3px solid #00f;
}
.pyg_view .left_box .left_memu li a {
  display: block;
  width: 100%;
  height: 100%;
  color: #000;
}
.pyg_view .right_box {
  flex: 1;
  background-color: #fff;
}
.pyg_view .right_box > ul .right_item .item_title {
  height: 2.13333333rem;
  line-height: 2.13333333rem;
  padding-left: 15px;
  color: #f00;
  font-weight: 600;
}
.pyg_view .right_box > ul .right_item .item_list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.pyg_view .right_box > ul .right_item .item_list li {
  width: 33.33%;
}
.pyg_view .right_box > ul .right_item .item_list li a {
  display: block;
  text-align: center;
}
.pyg_view .right_box > ul .right_item .item_list li a img {
  width: 80%;
}
.pyg_view .right_box > ul .right_item .item_list li a .item_name {
  color: #000;
  font-size: 0.74666667rem;
}
